import timePicker from '../../components/wing-time-selector/wing-time-selector.vue';
export default {
	name: "",
	components: {
		timePicker
	},
	props: {},
	data() {
		return {
			birth: '',
			// 时间选择器的显示
			showFlag: false
		}
	},
	methods: {
		showModal() {
			this.showFlag = true
		},
		// 时间选择器的确定按钮
		btnConfirm(e) {
			// console.log(e.key);
			this.birth = e.key
		},
		changeBirth() {
			this.$store.state.userObj.birthday = this.birth
			this.$changes()
		},
		// 取消按钮
		btnCancel() {

		}
	},
	mounted() {
		if (uni.getStorageSync('userinfo')) {
			this.$store.state.userObj = JSON.parse(uni.getStorageSync('userinfo'))
		}
	},
	onLoad(option) {
		this.birth = option.birth
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
